import {useEffect, useRef, useState} from "react";
import {Box, IconButton} from "@mui/joy";
import VolumeUpIcon from '@mui/icons-material/VolumeUp';
import VolumeOffIcon from '@mui/icons-material/VolumeOff';
import poster from '../assets/videoPoster.png';

const Video = ({video, circle, pulse}) => {
  const circleClass = circle ? 'rounded-full' : '';
  const [muted, setMuted] = useState(true);
  const ref = useRef(null);
  useEffect(() => {
    ref.current.muted = true;
    ref.current.controls = false;
    ref.current.autoPlay = true;
    ref.current.addEventListener('play', function () {
      this.autoPlay = true;
      this.controls = false;
    });

    ref.current.addEventListener('pause', function () {
      this.autoPlay = false;
      this.controls = false;
    });
  }, []);
  const toggleMute = () => {
    const isMuted = ref.current.muted;
    ref.current.muted = !isMuted;
    setMuted(!isMuted);
  }
  return (
    <Box onClick={toggleMute} sx={{
      position: 'relative',
      display: 'block',
    }}
         className={'cursor-pointer ' + (circle ? "w-56 h-56" : '')}
    >
      {pulse &&
        <div className={'w-full h-full bg-accent-500 opacity-15 animate-ping absolute z-0 top-0 right-0 ' + circleClass}/>}
      <video poster={poster} preload={'none'} onClick={(e) => e.preventDefault()} ref={ref}
             controls={false}
             className={'w-full h-full z-10 relative object-cover ' + circleClass}
             muted
             autoPlay
             playsInline
             loop
      >
        <source src={video} type="video/mp4"/>
      </video>
      <IconButton sx={{
        position: 'absolute',
        display: 'flex',
      }} variant={'soft'} color={'primary'}
                  className={'bottom-5 z-20 right-1/2 transform translate-x-1/2'}>
        {muted ? <VolumeUpIcon/> : <VolumeOffIcon/>}
      </IconButton>
    </Box>
  );
};

export default Video;